body, button, input {
  font-family: 'Roboto Condensed', sans-serif;
  color: #FBFFFE;
  color: var(--foreground);
}

button {
  width: 100%;
  display: block;
  background: none;
  border: none;
  outline: none;
  text-align: left;
  font-size: 1rem;
  margin: 0;
  padding: 0;
  line-height: 1.5em;
  cursor: pointer;
}

body {
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
}

 :root {
  --background: #1B1B1B;
  --foreground: #FBFFFE;
  --menu: #121212;
  --primary: #FF8000;
  --primary-hover: #FFC837;
  --secondary: #009FFD;

  --error: #F15025;

  --white-50: rgba(255, 255, 255, 0.5);
  --white-30: rgba(255, 255, 255, 0.3);
  --white-10: rgba(255, 255, 255, 0.1);
  --white-05: rgba(255, 255, 255, 0.05);
  --white-02: rgba(255, 255, 255, 0.02);

  --black-50: rgba(0, 0, 0, 0.5);
  --black-30: rgba(0, 0, 0, 0.3);
  --black-10: rgba(0, 0, 0, 0.1);
  --black-05: rgba(0, 0, 0, 0.05);

  --scroll-background: var(--background);
  --scroll-thumb: #2F2F2F;
  --scroll-thumb-hover: #444444;

  --font-logo: 'Chewy', sans-serif;
 }

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background-color: #1B1B1B;
  background-color: var(--scroll-background);
}

::-webkit-scrollbar-thumb {
  min-height: 50px;
  background-color: #2F2F2F;
  background-color: var(--scroll-thumb);
}

::-webkit-scrollbar-thumb:hover { 
  background-color: #444444; 
  background-color: var(--scroll-thumb-hover);
}

.Root {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  background: var(--background);
  color: var(--foreground);
}

.Root-contents {
  -webkit-flex: 1 1;
          flex: 1 1;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  overflow: hidden;
}


.Loading {
  -webkit-flex: 1 1;
          flex: 1 1;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.LoginWithPin {
  -webkit-flex: 1 1;
          flex: 1 1;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 2em;
  overflow: auto;
  position: relative;

  background: #FF8008;
  background: -webkit-linear-gradient(right, #FF8008 , #FFC837);
  background: linear-gradient(to left, #FF8008 , #FFC837);
}

.LoginWithPin-form {
  position: relative;
  z-index: 1;
  width: 400px;
  background: var(--black-10);
  padding: 2em;
  box-sizing: border-box;
  text-align: center;
}

.LoginWithPin-tagline {
  text-align: center;
  font-family: var(--font-logo);
  font-size: 2em;
  margin: 0;
}

.LoginWithPin-logo {
  text-align: center;
  font-family: var(--font-logo);
  font-size: 5em;
  margin: 0;
  letter-spacing: 2px;
}

.LoginWithPin-pin {
  font-size: 5em;
  margin: 0;
  text-align: center;
}

.LoginWithPin-button {
  width: auto;
  color: var(--background);
  background: var(--primary);
  padding: 0.5em 1em;
  border-radius: 2px;
}

.LoginWithPin-button:hover {
  background: var(--primary-hover);
}

.Settings {
  -webkit-flex: 1 1;
          flex: 1 1;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  color: #FBFFFE;
  background: #1B1B1E;
  padding: 2em;
  overflow: auto;
}

.Settings-contents {
  -webkit-flex: 1 1;
          flex: 1 1;
  max-width: 600px;
}

.Settings-contents:after {
  content: '';
  display: block;
  height: 5em;
}

.Settings-sectionHeader {
  font-size: 1.5em;
  padding-bottom: 0.5em;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  margin-bottom: 0;
}

.Settings-doneButton {
  display: block;
  color: var(--white);
  text-decoration: none;
  text-align: center;
  padding: 0.5em 1em;
  border: 1px solid var(--white-05);
  margin: 1em 0;
}

.Settings-doneButton:hover {
  background: var(--white-05);
}

.Settings-footer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.Settings-logoutButton {
  display: -webkit-flex;
  display: flex;
  width: auto;
  color: var(--white-50);
  text-decoration: none;
}

.Settings-logoutButton:hover {
  color: var(--white);
  text-decoration: underline;
}

.Settings-version {
  color: var(--white-50);
}

